import React from 'react'
import {InjectedFormProps, Field, reduxForm} from "redux-form";
import renderTextField from "../common/elements/RenderTextField";
import {Button} from '@material-ui/core';
import validate from './RegistrationValidate';
import {authRegProfileType} from "../../types/types";


const Registration: React.FC<InjectedFormProps> = ({handleSubmit}) => {
    return (
        <form onSubmit={handleSubmit}>
            <Field name='surname' component={renderTextField} label='' placeholder=' ' />
            <Field name='forename' component={renderTextField} label='*' placeholder=' ' />
            <Field name='patronymic' component={renderTextField} label='' placeholder=' ' />
            <Field name='phone' component={renderTextField} label='*' placeholder='+7(999)999-99-99' />
            <Field name='email' component={renderTextField} label='Email*' placeholder=' email' />
            <Field name='password' component={renderTextField} type='password' label='*' placeholder=' ' />
            <Field name='confirmPassword' component={renderTextField} type='password' label=' *' placeholder=' '  />
            <Button variant='contained' type='submit'></Button>
        </form>
    )
}

let registrationForm = reduxForm<authRegProfileType>({form: 'registrationForm', validate})(Registration)

export default registrationForm;